@import url('https://fonts.googleapis.com/css?family=Amatic+SC');
.side-menu-container .btn-open-side-menu {
  position: fixed;
  padding: 1em;
  top: 0;
  right: 0;
  opacity: 0.15;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
.side-menu-container .btn-open-side-menu svg {
  stroke: #fff4c2;
  height: 50px;
}
.side-menu-container .btn-open-side-menu:hover {
  opacity: 0.30;
}
.side-menu-container .side-menu {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  background-color: #1a1a1a;
  opacity: 0.95;
  overflow-x: hidden;
  padding-top: 40px;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.side-menu-container .side-menu a {
  padding: 10px;
  text-decoration: none;
  font-size: 22px;
  color: #fff4c2;
  display: block;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}
.side-menu-container .side-menu a:hover {
  color: #fff;
}
.side-menu-container .side-menu .content {
  position: relative;
  padding: 20px 10px 20px 30px;
  font-size: 1.3em;
  overflow: hidden;
}
.side-menu-container .side-menu .btn-close {
  position: absolute;
  top: -15px;
  left: 10px;
  font-size: 6em;
}
.FillScreen {
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
}
.wake-up-container {
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  position: absolute;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.wake-up-container .backDrop {
  position: absolute;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 80%;
  height: 80vh;
  max-width: 650px;
  max-height: 100vw;
  margin: auto;
}
.wake-up-container .backDrop {
  overflow-y: hidden;
  padding: 6vw;
  -webkit-transition: 2s;
  -o-transition: 2s;
  transition: 2s;
}
@media (min-width: 900px) {
  .wake-up-container .backDrop {
    padding: 54px;
  }
}
.wake-up-container .backDrop .wake-up-console {
  font-size: 9vw;
  line-height: 1em;
  text-transform: uppercase;
  font-family: 'Amatic SC', cursive;
  text-shadow: 0.2px 0.2px 6px rgba(0, 0, 0, 0.2);
}
@media (min-width: 900px) {
  .wake-up-container .backDrop .wake-up-console {
    font-size: 81px;
  }
}
.wake-up-container .backDrop .wake-up-console .vertical-bar {
  display: -ms-inline-flexbox;
  display: inline-flex;
  background-color: #fff4c2;
  height: 0.9em;
  width: 0.5em;
  position: relative;
  top: 0.1em;
  left: 0.1em;
  -webkit-animation: blinker 1s linear infinite;
          animation: blinker 1s linear infinite;
}
.blink_me {
  -webkit-animation: blinker 1s linear infinite;
          animation: blinker 1s linear infinite;
}
@-webkit-keyframes blinker {
  50% {
    opacity: 0;
  }
}
@keyframes blinker {
  50% {
    opacity: 0;
  }
}
.appContainer {
  color: #fff4c2;
  font-family: monospace;
}
body {
  background-color: black;
}



